import { FC } from 'react';
import s from './index.module.css';

type ButtonProps = {
  text: string;
  disabled?: boolean;
  className?: string;
  buttonClassName?: string;
  onClick: () => void;
};

const Button: FC<ButtonProps> = ({
  text,
  onClick,
  buttonClassName,
  className,
  disabled,
}) => {
  console.log('[D] text, buttonClass: ', text, buttonClassName);

  const buttonClass = buttonClassName
    ? `${s.button} ${s.buttonClassName}`
    : s.button;


  return (
    <div className={className}>
      <button
        className={buttonClass}
        onClick={onClick}
        disabled={disabled}
      >
        {text}
      </button>
    </div>
  );
};

Button.defaultProps = {
  className: '',
  buttonClassName: '',
  disabled: false,
};

export default Button;
